import { useState, useEffect } from 'react';

export const TimerComponent = ({ winner }: { winner: boolean}) => {
    const [gameTime, setGameTime] = useState(0);
    const [running, setRunning] = useState(false);
    
    useEffect(() => {
      let interval: NodeJS.Timeout;
      if (winner) {
        setGameTime(0);
        setRunning(true);
      } else {
        setRunning(false);
      }
      return () => {
        if (interval) {
          clearInterval(interval);
        }}
    }, [ winner]);

    useEffect(() => {
      let interval: NodeJS.Timeout;
      if (running) {
        interval = setInterval(() => {
          setGameTime((prev) => prev + 0.01);
        }, 10);
      }
      return () => {
        if (interval) {
          clearInterval(interval);
        }}
    }, [running]);
    
    return <div>{gameTime.toFixed(2)}</div>;
  };
  
